<template>
    <div class="select-tpl">
        <vue-scroll>
            <div class="tpl-list" v-if="data.hasOwnProperty('extendList')">
                <div class="tpl-item" v-for="item in data.extendList" @click="selectTpl(item.id)">
                    <div class="selected" :class="{active:item.id === data.promotion.id}" v-if="data.hasOwnProperty('promotion')"><span
                            class="el-icon-check"></span></div>
                    <diy-image :src="item.img" class="item-img" fit="contain"></diy-image>
                </div>
            </div>
        </vue-scroll>
    </div>
</template>

<script>
    import DiyImage from "../common/diyImage";
    export default {
        name: "selectTpl",
        components: {DiyImage},
        props:{
            data:{
                type:Object,
                default:()=>{
                    return {
                        extendList:[],
                        promotion:{},
                    };
                }
            }
        },
        methods:{
            selectTpl(id){
                this.$emit('closeDialog');
                if(typeof this.data.changeFunc === "function"){
                    this.data.changeFunc(id);
                }
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../assets/css/common/lib.scss";
    .select-tpl{
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        z-index: 100;
        overflow: hidden;
        border-top: 1px solid #DCDFE6;

        .tpl-list{
            position: relative;
            padding: 20px 20px 10px 20px;

            .tpl-item{
                position: relative;
                text-align: center;
                .selected {
                    position: absolute;
                    width: 35px;
                    height: 35px;
                    line-height: 35px;
                    overflow: hidden;
                    border-radius: 35px;
                    font-size: 20px;
                    color: #ffffff;
                    text-align: center;
                    bottom: 30px;
                    right: 30px;
                    background-color: #67C23A;
                    border: 1px solid rgba(255, 255, 255, 0.3);
                    opacity: 0;
                    z-index: 100;

                    &.active {
                        opacity: 1;
                    }
                }

                .item-img{
                    width: 90%;
                    margin-bottom: 10px;
                    border: 1px solid #F2F6FC;
                    z-index: 10;

                    &:active{
                        opacity: 0.8;
                    }
                }
            }
        }
    }
</style>