<template>
    <div class="edit-tpl" v-if="promotion">
        <vue-scroll>
            <div class="info-main">
                <div class="info-item">
                    <div class="item-title">{{$L('默认头像')}}</div>
                    <div class="item-box">
                        <el-select v-model="promotion.headType" class="input-item" size="small">
                            <el-option value="1" :label="$L('使用会员头像')"></el-option>
                            <el-option value="2" :label="$L('使用分销商店铺LOGO')"></el-option>
                            <el-option value="0" :label="$L('不使用头像')"></el-option>
                        </el-select>
                    </div>
                </div>

                <div class="info-item">
                    <div class="item-title">
                        <span class="title-txt">{{$L('页面背景')}}</span>
                        <el-checkbox v-model="promotion.isShowBg" true-label="1" false-label="0"></el-checkbox>
                    </div>
                    <div class="item-box">
                        <diy-image class="bg-img" :src="promotion.bgImage" :error-text="$L('点击上传')"></diy-image>
                        <div class="bg-settings">
                            <label>
                                <el-button size="small" class="settings-item">{{ $L('上传图片') }}</el-button>
                                <input class="file-input" type="file" name="CommentsPic"
                                       :placeholder="$L('上传图片')" multiple
                                       accept="image/png,image/jpeg,image/jpg" @change="upImgFunc"/>
                            </label>
                            <el-button size="small" class="settings-item" @click="defaultImg">{{ $L('默认背景') }}</el-button>
                            <el-color-picker size="small" class="settings-item" v-model="promotion.bgColor"></el-color-picker>

                        </div>
                        <div class="tips">{{ $L('建议尺寸:375x560px，小于2M，PNG、JPG格式') }}</div>
                    </div>
                </div>

                <div class="info-item">
                    <div class="item-title">
                        <span class="title-txt">{{$L('大标题')}}</span>
                        <el-checkbox v-model="promotion.isTitle" true-label="1" false-label="0"></el-checkbox>
                    </div>
                    <div class="item-box">
                        <el-input v-model="promotion.extTitle" size="small" class="input-item"></el-input>
                        <el-select v-model="promotion.extTSize" size="small" class="mini-input">
                            <el-option
                                    v-for="item in options"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                            </el-option>
                        </el-select>
                        <el-select v-model="promotion.extTFont" size="small" class="mini-input">
                            <el-option
                                    v-for="item in optionsFam"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                            </el-option>
                        </el-select>
                        <el-color-picker v-model="promotion.extTColor" size="small" class="color"></el-color-picker>
                    </div>
                </div>

                <div class="info-item">
                    <div class="item-title">
                        <span class="title-txt">{{$L('副标题')}}</span>
                        <el-checkbox v-model="promotion.isSubTitle" true-label="1" false-label="0"></el-checkbox>
                    </div>
                    <div class="item-box">
                        <el-input v-model="promotion.extSubTitle" size="small" class="input-item"></el-input>
                        <el-select v-model="promotion.extSubTSize" size="small" class="mini-input">
                            <el-option
                                    v-for="item in options"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                            </el-option>
                        </el-select>
                        <el-select v-model="promotion.extSubTFont" size="small" class="mini-input">
                            <el-option
                                    v-for="item in optionsFam"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                            </el-option>
                        </el-select>
                        <el-color-picker v-model="promotion.extSubTColor" size="small" class="color"></el-color-picker>
                    </div>
                </div>

                <div class="info-item">
                    <div class="item-title">
                        <span class="title-txt">{{$L('提示文本1')}}</span>
                        <el-checkbox v-model="promotion.isTip1" true-label="1" false-label="0"></el-checkbox>
                    </div>
                    <div class="item-box">
                        <el-input v-model="promotion.extTip1" size="small" class="input-item"></el-input>
                        <el-select v-model="promotion.extTSize1" size="small" class="mini-input">
                            <el-option
                                    v-for="item in options"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                            </el-option>
                        </el-select>
                        <el-select v-model="promotion.extTFont1" size="small" class="mini-input">
                            <el-option
                                    v-for="item in optionsFam"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                            </el-option>
                        </el-select>
                        <el-color-picker v-model="promotion.extTColor1" size="small" class="color"></el-color-picker>
                    </div>
                </div>

                <div class="info-item">
                    <div class="item-title">
                        <span class="title-txt">{{$L('提示文本2')}}</span>
                        <el-checkbox v-model="promotion.isTip2" true-label="1" false-label="0"></el-checkbox>
                    </div>
                    <div class="item-box">
                        <el-input v-model="promotion.extTip2" size="small" class="input-item"></el-input>
                        <el-select v-model="promotion.extTSize2" size="small" class="mini-input">
                            <el-option
                                    v-for="item in options"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                            </el-option>
                        </el-select>
                        <el-select v-model="promotion.extTFont2" size="small" class="mini-input">
                            <el-option
                                    v-for="item in optionsFam"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                            </el-option>
                        </el-select>
                        <el-color-picker v-model="promotion.extTColor2" size="small" class="color"></el-color-picker>
                    </div>
                </div>

                <div class="info-item" v-if="promotion.pid == 3">
                    <div class="item-title">
                        <span class="title-txt">{{$L('优势标题')}}</span>
                        <el-checkbox v-model="promotion.isAdvTitle" true-label="1" false-label="0"></el-checkbox>
                    </div>
                    <div class="item-box">
                        <el-input v-model="promotion.advTitle" size="small" class="input-item" style="width:calc(100% - 42px);"></el-input>
                        <el-color-picker v-model="promotion.advTColor" size="small" style="top: 0; position: absolute; right:0;"></el-color-picker>
                    </div>
                </div>

                <div class="info-item" v-if="promotion.pid == 3">
                    <div class="item-title">
                        <span class="title-txt">{{$L('优势说明')}}</span>
                        <el-color-picker v-model="promotion.advContentColor" size="mini" class="color-set"></el-color-picker>
                    </div>
                    <div class="item-box" style="margin-top: 5px;">
                        <div v-for="(item,index) in promotion.advContent" :key="index">
                            <span class="plant-num">{{index+1}}</span>
                            <el-input v-model="promotion.advContent[index]" size="small" class="plant-txt"></el-input>
                            <div class="poster-add" @click="handleAdd" v-if="index == 0">
                                <i class="el-icon-plus i"></i>
                            </div>
                            <div class="poster-add" @click="handleDel(index)" v-else>
                                <i class="el-icon-minus i"></i>
                            </div>
                        </div>
                    </div>
                </div>

                <div class="info-item" v-if="promotion.pid == 3">
                    <div class="item-title">
                        <span class="title-txt">{{$L('描述说明')}}</span>
                        <el-checkbox v-model="promotion.isNoteContent" true-label="1" false-label="0"></el-checkbox>
                    </div>
                    <div class="item-box">
                        <el-input type="textarea" v-model="promotion.noteContent"
                                  rows="4"></el-input>
                    </div>
                </div>
            </div>
            <div class="bottom">
                <el-button type="primary" class="button" size="small" @click="save">{{ $L('保存') }}</el-button>
            </div>
        </vue-scroll>
    </div>
</template>

<script>
    import ShareImg from "../../components/distribution/shareImg";
    import DiyImage from "../common/diyImage";

    export default {
        name: "editTpl",
        components: {DiyImage, ShareImg},
        props: {
            data: {
                type: Object,
                default: () => {
                    return {};
                }
            }
        },
        computed:{
            promotion:{
                get(){
                    if(this.data.hasOwnProperty('promotion')){
                        this.promotionData = this.$clone(this.data.promotion);
                    }
                    return this.promotionData;
                },
                set(val){
                    this.promotionData = val;
                }
            }
        },
        data() {
            return {
                promotionData:null,
                options: [
                    {
                        value: '12px',
                        label: '12px'
                    },
                    {
                        value: '14px',
                        label: '14px'
                    },
                    {
                        value: '16px',
                        label: '16px'
                    },
                    {
                        value: '18px',
                        label: '18px'
                    },
                    {
                        value: '20px',
                        label: '20px'
                    },
                    {
                        value: '24px',
                        label: '24px'
                    },
                    {
                        value: '28px',
                        label: '28px'
                    },
                    {
                        value: '30px',
                        label: '30px'
                    },
                    {
                        value: '32px',
                        label: '32px'
                    },
                    {
                        value: '34px',
                        label: '34px'
                    },
                    {
                        value: '36px',
                        label: '36px'
                    },
                    {
                        value: '48px',
                        label: '48px'
                    },
                    {
                        value: '60px',
                        label: '60px'
                    },
                    {
                        value: '70px',
                        label: '70px'
                    },
                ],
                optionsFam: [
                    {
                        value: '微软雅黑',
                        label: '微软雅黑'
                    },
                    {
                        value: '宋体',
                        label: '宋体'
                    },
                    {
                        value: '新宋体',
                        label: '新宋体'
                    },
                    {
                        value: '仿宋_GB2312',
                        label: '仿宋_GB2312'
                    },
                    {
                        value: '楷体_GB2312',
                        label: '楷体_GB2312'
                    },
                    {
                        value: '黑体',
                        label: '黑体'
                    },
                    {
                        value: 'Arial',
                        label: 'Arial'
                    },
                    {
                        value: 'Arial Black',
                        label: 'Arial Black'
                    },
                    {
                        value: 'Times New Roman',
                        label: 'Times New Roman'
                    },
                    {
                        value: 'Courier New',
                        label: 'Courier New'
                    },
                    {
                        value: 'Tahoma',
                        label: 'Tahoma'
                    },
                    {
                        value: 'Verdana',
                        label: 'Verdana'
                    }
                ]
            }
        },
        methods: {
            save(){
                this.$emit('closeDialog');
                if(this.data.hasOwnProperty('saveFunc')){
                    this.data.saveFunc(this.promotion);
                }
            },
            defaultImg() {
                if (this.promotion.hasOwnProperty('defaultBgImage')) {
                    this.promotion.bgImage = this.promotion.defaultBgImage;
                }
            },
            upImgFunc(event) {
                const _self = this;
                let files = event.target.files[0];
                if (files && _self.data.hasOwnProperty('promotion')) {
                    let size = Math.floor(files.size / 1024);
                    let type = files.type.substring(6);
                    if (size > 2 * 1024 * 1024) {
                        _self.$message({
                            message: _self.$L('请选择2M以内的图片！'),
                            type: 'warning'
                        });
                        return false;
                    }
                    if (!(type === 'png' || type === 'jpeg' || type === 'jpg')) {
                        _self.$message({
                            message: _self.$L('请选择png、jpeg、jpg的图片格式！'),
                            type: 'warning'
                        });
                        return false;
                    }
                    let reader = new FileReader();
                    reader.readAsDataURL(files);
                    reader.onload = function (evt) {
                        _self.promotion.bgImage = evt.target.result;
                    }
                }
            },
            handleAdd() {
                if (this.promotion) {
                    this.promotion.advContent.splice(this.promotion.advContent.length, 0,
                        this.$L('优势说明')
                    );
                }
            },
            handleDel(index) {
                if (this.promotion) {
                    this.promotion.advContent.splice(index, 1);
                }
            },
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../assets/css/common/lib.scss";

    .edit-tpl {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        z-index: 100;
        overflow: hidden;
        border-top: 1px solid #DCDFE6;

        .info-main {
            position: relative;
            padding: 20px;
            width: 100%;

            .info-item{
                position: relative;
                width: 100%;
                clear: both;
                margin-bottom: 20px;

                .item-title{
                    position: relative;
                    font-size: 14px;
                    line-height: 30px;
                    text-align: left;

                    .title-txt{
                        margin-right: 10px;
                    }

                    .color-set{
                        top: 10px;
                    }
                }

                .item-box{
                    position: relative;
                    font-size: 14px;
                    line-height: 30px;
                    text-align: left;

                    .input-item{
                        width: 100%;
                    }

                    .bg-img{
                        position: relative;
                        width: 80px;
                        height: 80px;
                    }

                    .bg-settings{
                        position: absolute;
                        width: calc(100% - 80px);
                        left: 80px;
                        top: 0;
                        z-index: 100;
                        height: 35px;
                        line-height: 35px;

                        .settings-item{
                            position: relative;
                            margin-left: 10px;
                            display: block;
                            float: left;
                        }

                        .file-input{
                            position: absolute;
                            top: 0;
                            left: 10px;
                            z-index: 200;
                            width: 80px;
                            height: 32px;
                            opacity: 0;
                        }
                    }

                    .tips{
                        position: absolute;
                        width: calc(100% - 90px);
                        left: 90px;
                        top: 45px;
                        z-index: 100;
                        line-height: 1.5;
                        font-size: 12px;
                        color: #909399;
                    }

                    .mini-input{
                        margin-right: 10px;
                        max-width: 30%;
                    }

                    .color{
                        top: 12px;
                    }

                    .poster-add {
                        display: inline-block;
                        margin-left: 10px;
                        vertical-align: baseline;
                        width: 32px;
                        height: 32px;
                        background: #eee;
                        text-align: center;
                        border-radius: 5px;
                        cursor: pointer;

                        .i {
                            height: 32px;
                            line-height: 32px;
                        }
                    }

                    .plant-num{
                        margin-right: 10px;
                        color: #909399;
                    }

                    .plant-txt{
                        display: inline-block;
                        width: calc(100% - 60px);
                        margin-bottom: 5px;
                    }
                }
            }
        }

        .bottom{
            position: relative;
            width: 100%;
            height: 60px;
            line-height: 60px;
            text-align: center;
            background-color: #F2F2F2;

            .button{
                width: 80%;
            }
        }
    }
</style>
<style lang="scss" scoped>
    @media screen and (max-width:320px) {
        .bg-img {
            width: 60px !important;
            height: 60px !important;
        }
        .bg-settings{
            width: calc(100% - 60px) !important;
            left: 60px !important;
            .settings-item{
                margin-left: 5px !important;
            }
            .file-input{
                width: 70px !important;
                left: 5px !important;
            }
        }
        .tips{
            left: 70px !important;
        }
        .info-main{
            padding: 12px;
        }
    }
</style>