<template>
    <div class="promotion-settings" v-loading="loading">
        <div class="no-data" v-if="extendList.length<1">
            {{ $L('暂无数据') }}
        </div>
        <div v-else class="settings-box">
            <vue-scroll style="bottom: 60px;">
                <div class="select-tpl" @click="doSelectTpl">
                    {{$L('选择海报')}}
                    <div class="icon">
                        <span class="el-icon-arrow-right"></span>
                    </div>
                </div>
                <div class="tpl-box">
                    <div class="tpl-img" ref="showBox">
                        <share-img :promotion="promotion"></share-img>
                    </div>
                </div>
            </vue-scroll>
            <div class="settings-bottom">
                <el-button class="button" @click="downImg">{{ $L('下载海报') }}</el-button>
                <el-button type="primary" class="button" @click="doEditTpl">{{ $L('编辑海报') }}</el-button>
            </div>
        </div>
    </div>
</template>

<script>
    import defaultUserImg from "../../../../assets/img/common/default_user.jpg";
    import ShareImg from "../../../../components/distribution/shareImg";
    import SelectTpl from "../../../../components/distribution/selectTpl";
    import EditTpl from "../../../../components/distribution/editTpl";
    import DownloadImg from "../../../../components/common/downloadImg";

    export default {
        name: "promotionSettings",
        components: {ShareImg},
        data() {
            return {
                loading: true,
                extendList: [],
                promotion: {},
                newImage:null
            }
        },
        created() {
            this.getData();
        },
        methods: {
            doSelectTpl() {
                this.$store.commit('setDialogObj', {
                    name: this.$L('选择海报'),
                    view: SelectTpl,
                    data: {
                        extendList: this.extendList,
                        promotion: this.promotion,
                        changeFunc: (id) => {
                            this.newImage = null;
                            this.getData(id);
                        }
                    }
                });
            },
            doEditTpl() {
                this.$store.commit('setDialogObj', {
                    name: this.$L('编辑海报'),
                    view: EditTpl,
                    data: {
                        promotion: this.promotion,
                        saveFunc: (data) => {
                            this.promotion = data;
                            this.doSave();
                        }
                    }
                });
            },
            getData(id) {
                const _self = this;
                _self.loading = true;
                setTimeout(() => {
                    _self.$getApi('distributionShopSeoSetting', {id: id}, function (reData) {
                        _self.extendList = reData.extendList;
                        _self.promotion = reData.promotion;
                        _self.changeTemplateUrl(() => {
                            _self.loading = false;
                        });
                    }, function (error) {
                        console.log(error);
                        _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData(id);
                        }).catch(action => {
                            _self.$router.push({name: 'home'});
                        });
                    });
                }, 300);
            },
            changeTemplateUrl(func) {
                let c = 4;
                let d = 0;
                let doFunc = () => {
                    d++;
                    if (d === c && typeof func === "function") {
                        func();
                    }
                };
                if (this.promotion.hasOwnProperty('bgImage')) {
                    this.$getBase64(this.promotion.bgImage, (data) => {
                        this.promotion.bgImage = data;
                        doFunc();
                    });
                } else {
                    doFunc();
                }
                if (this.promotion.hasOwnProperty('userHead')) {
                    if (!this.promotion.userHead) {
                        this.promotion.userHead = defaultUserImg;
                    }
                    this.$getBase64(this.promotion.userHead, (data) => {
                        this.promotion.userHead = data;
                        doFunc();
                    });
                } else {
                    doFunc();
                }
                if (this.promotion.hasOwnProperty('shopHead')) {
                    this.$getBase64(this.promotion.shopHead, (data) => {
                        this.promotion.shopHead = data;
                        doFunc();
                    });
                } else {
                    doFunc();
                }
                if (this.promotion.hasOwnProperty('qrCode')) {
                    this.$getBase64(this.promotion.qrCode, (data) => {
                        this.promotion.qrCode = data;
                        doFunc();
                    });
                } else {
                    doFunc();
                }
            },
            downImg() {
                this.loading = true;
                if(this.newImage === null){
                    setTimeout(() => {
                        this.$makeImg(this.$refs.showBox, {
                            backgroundColor: null
                        }).then((canvas) => {
                            this.newImage = canvas.toDataURL();
                            this.$store.commit('setDialogObj', {
                                name: this.$L('长按图片可保存'),
                                view: DownloadImg,
                                data: {
                                    img: this.newImage
                                }
                            });
                            this.loading = false;
                        });
                    }, 500);
                }else{
                    this.$store.commit('setDialogObj', {
                        name: this.$L('长按图片可保存'),
                        view: DownloadImg,
                        data: {
                            img: this.newImage
                        }
                    });
                    this.loading = false;
                }
            },
            doSave() {
                this.loading = true;
                setTimeout(() => {
                    this.$makeImg(this.$refs.showBox, {
                        backgroundColor: null
                    }).then((canvas) => {
                        this.newImage = this.promotion.extendImg = canvas.toDataURL();
                        this.updateData();
                    });
                }, 1000);
            },
            updateData() {
                const _self = this;
                _self.loading = true;
                setTimeout(() => {
                    _self.$getApi('shopSeoSettingSave', {promotion: _self.promotion}, function (reData) {
                        _self.extendList = reData.extendList;
                        _self.promotion = reData.promotion;
                        _self.changeTemplateUrl(() => {
                            _self.$message({
                                message: _self.$L('操作成功'),
                                type: 'success'
                            });
                            _self.loading = false;
                        });
                    }, function (error) {
                        console.log(error);
                        _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.updateData();
                        }).catch(action => {
                            _self.$router.push({name: 'home'});
                        });
                    });
                }, 300);
            },
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../../../assets/css/common/lib.scss";

    .promotion-settings {
        position: relative;
        width: 100%;
        height: 100%;
        overflow: hidden;

        .settings-box {
            position: absolute;
            width: 100%;
            height: 100%;
            left: 0;
            top: 0;
            z-index: 100;
            overflow: hidden;

            .select-tpl {
                position: relative;
                width: 100%;
                height: 45px;
                line-height: 45px;
                padding: 0 15px;
                font-size: 14px;
                background-color: #ffffff;
                border-bottom: 1px solid #F2F2F2;
                cursor: pointer;
                @extend .no-select;
                color: #606266;
                margin-top: 60px;

                .icon {
                    position: absolute;
                    height: 100%;
                    width: 45px;
                    top: 0;
                    right: 0;
                    text-align: center;
                    font-size: 18px;
                    color: #909399;
                }

                &:active {
                    background-color: #F2F6FC;
                }
            }

            .tpl-box {
                position: relative;
                padding: 20px 0;
                background-color: #ffffff;

                .tpl-img {
                    position: relative;
                    width: 300px;
                    margin: 0 auto;
                }
            }
        }

        .no-data {
            color: #999;
            font-size: 14px;
            text-align: center;
            height: 50px;
            line-height: 50px;
            background: #fff;
        }

        .settings-bottom {
            position: absolute;
            width: 100%;
            height: 60px;
            left: 0;
            bottom: 0;
            z-index: 100;
            text-align: center;
            line-height: 60px;
        }
    }
</style>